import React from 'react'
import {
    MDBContainer,
    MDBNavbar,
    MDBNavbarBrand,
    MDBBtn,
    MDBInputGroup
} from 'mdb-react-ui-kit';

function TopBar() {
    return (
        <div style={{ fontFamily: 'Josefin+Sans' }}>
            <MDBNavbar light bgColor='light'>
                <MDBContainer className='container' fluid>
                    <MDBNavbarBrand className='fw-bold text-primary'><img style={{ width: '40px', borderRadius: '10px', marginRight: '10px' }} src="data:image/jpeg;base64," alt="" />Discount Calculator</MDBNavbarBrand>
                    <MDBInputGroup tag="form" className='d-flex w-auto mb-3'>
                        <input className='form-control' placeholder="Search here..." aria-label="Search" type='Search' />
                        <MDBBtn outline>Search</MDBBtn>
                    </MDBInputGroup>
                </MDBContainer>
            </MDBNavbar>
        </div>
    )
}

export default TopBar